
import Header from './Header';
import ProjectsContainer from './ProjectsContainer';
import AOS from 'aos';
import Skills from './Skills';
import SendMessage from './SendMessage';
import AppContainer from './AppContainer'
import { useEffect, useState } from 'react';
import Navbar from './Navbar';
import MobileNav from './MobileNav';
AOS.init();
const data=[{
  id:1,
  link:"https://github.com/pentekmate/WebCrawler_Frontend",
  nev:"Webcrawler bot",
  kep:"Frame10.png",
  leiras:"A project lnyege az volt , hogy bizonyos hasznlt cikkek megtallsa gyorsabb legyen, s ne kelljen oldalrloldalra jrklni.",
  technologia:["ReactJs","NodeJs","Firebase"]
},
{
  id:2,
  link:"https://github.com/pentekmate/Bevasarlolista_Kozos_frontend",
  nev:"Bevsrl listm",
  kep:"Frame4.png",
  leiras:"Ebben a projectben egy mobil alkalmazst ksztettnk.Az alkalmazssal a besrlst szerettk volna felgyorstani. A projectet egy msik trsammal ksztettk. ",
  technologia:["React native","NodeJs","Mysql"] 
},
{
  id:3,
  link:"",
  kep:"Frame11.png",
  nev:"Astronomy Page",
  leiras:"Egyszer landing page csillagszattal kapcsolatban. Hasznlva a NASA API-t. Ezaz alkalmazs mg fejlesztsi fzisban van. A rla lthat kpek figma tervek.",
  technologia:["ReactJs"] 
},]
function App() {
  const [screenSize,setScreenSize]=useState(getCurrentScreenSize())
  const [scrolling, setScrolling] = useState(false);
  function getCurrentScreenSize()
  {
    return window.innerWidth;
  }
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 300) {
        setScrolling(true);
      } else {
        setScrolling(false);
      }
    };
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [window.scrollY]);
  useEffect(() => {
    const updateDimension = () => {
      setScreenSize(getCurrentScreenSize())
    }
    window.addEventListener('resize', updateDimension);
    
    return(() => {
        window.removeEventListener('resize', updateDimension);
      
    })
  }, [screenSize])
  return (
    <AppContainer>
        {screenSize>768? 
        <Navbar scrolling={scrolling}></Navbar>:
        <MobileNav scrolling={scrolling}></MobileNav>}
       
        <Header></Header>
        <ProjectsContainer data={data}></ProjectsContainer>
        <Skills></Skills>
        <SendMessage></SendMessage>
    </AppContainer>
  );
}

export default App;
